:root {
    --primary: #321fdb;
    --primaryHover: #2a1ab9;
    --secondary: #636f83;
  }
  
  body {
    font-family: "Open Sans", sans-serif;
    color: #2c313a;
  }
  
  a {
    color: var(--primary);
    text-decoration: none;
  }
  
  a:hover {
    color: var(--primaryHover);
    text-decoration: none;
  }
  
  .row-home {
    display: flex;
    justify-content: center;
  }
  
  @media (max-width: 1366px) {
    .row-home {
      display: block;
      justify-content: center;
    }
  }

  /*--------------------------------------------------------------
  # Header
  --------------------------------------------------------------*/
  #header {
    background: #fff;
    transition: all 0.5s;
    z-index: 997;
    height: 86px;
    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
    position: fixed;
    width: 100%;
  }
  
  #header.fixed-top {
    height: 70px;
  }
  
  #header .logo {
    font-size: 30px;
    margin: 0;
    padding: 0;
    line-height: 1;
    font-weight: 600;
    letter-spacing: 0.8px;
    font-family: "Poppins", sans-serif;
  }
  
  #header .logo a {
    color: #222222;
  }
  
  #header .logo a span {
    color: var(--primary);
  }
  
  #header .logo img {
    max-height: 60px;
  }
  
  .scrolled-offset {
    margin-top: 70px;
  }
  
  /*--------------------------------------------------------------
  # Sections General
  --------------------------------------------------------------*/
  section {
    padding: 60px 0;
    overflow: hidden;
  }
  
  .section-bg {
    background-color: #f6f9fe;
  }
  
  .section-title {
    text-align: center;
    padding-bottom: 30px;
  }
  
  .section-title h2 {
    font-size: 13px;
    letter-spacing: 1px;
    font-weight: 700;
    padding: 8px 20px;
    margin: 0;
    background: #e7f1fd;
    color: var(--primary);
    display: inline-block;
    text-transform: uppercase;
    border-radius: 50px;
  }
  
  .section-title h3 {
    margin: 15px 0 0 0;
    font-size: 32px;
    font-weight: 700;
  }
  
  .section-title h3 span {
    color: var(--primary);
  }
  
  .section-title p {
    margin: 15px auto 0 auto;
    font-weight: 600;
  }
  
  @media (min-width: 1024px) {
    .section-title p {
      width: 75%;
    }
  }
  
  /*--------------------------------------------------------------
  # Terms and Conditions
  --------------------------------------------------------------*/
  .terms .box {
    padding: 20px;
    background: #fff;
    text-align: center;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.12);
    border-radius: 5px;
    position: relative;
    overflow: hidden;
  }
  
  .terms .box h3 {
    font-weight: 400;
    margin: -20px -20px 20px -20px;
    padding: 20px 15px;
    font-size: 16px;
    font-weight: 600;
    color: #777777;
    background: #f8f8f8;
  }
  
  .terms .box h4 {
    font-size: 36px;
    color: var(--primary);
    font-weight: 600;
    font-family: "Poppins", sans-serif;
    margin-bottom: 20px;
  }
  
  .terms .box h4 sup {
    font-size: 20px;
    top: -15px;
    left: -3px;
  }
  
  .terms .box h4 span {
    color: #bababa;
    font-size: 16px;
    font-weight: 300;
  }
  
  .terms .box ul {
    padding: 0;
    list-style: none;
    color: #444444;
    text-align: center;
    line-height: 20px;
    font-size: 14px;
  }
  
  .terms .box ul li {
    padding-bottom: 16px;
  }
  
  .terms .box ul i {
    color: var(--primary);
    font-size: 18px;
    padding-right: 4px;
  }
  
  .terms .box ul .na {
    color: #ccc;
    text-decoration: line-through;
  }
  
  .terms .featured h3 {
    color: #fff;
    background: var(--primary);
  }
  
  .terms .advanced {
    width: 200px;
    position: absolute;
    top: 18px;
    right: -68px;
    transform: rotate(45deg);
    z-index: 1;
    font-size: 14px;
    padding: 1px 0 3px 0;
    background: var(--primary);
    color: #fff;
  }
  
  /*--------------------------------------------------------------
  # Footer
  --------------------------------------------------------------*/
  #footer {
    background: #fff;
    padding: 0 0 30px 0;
    color: #444444;
    font-size: 14px;
  }

  #footer .footerDIV {
    padding: 30px 0 0px;
    text-align: center;
  }
  
  #footer .footerDIV .footerLink:hover {
    cursor: pointer;
  }

  @media (max-width: 768px) {
    #footer .copyright, #footer .credits {
      float: none;
      text-align: center;
      padding: 2px 0;
    }
  }
  